body {
  box-sizing: border-box;
  width: 1100px;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  font-family: Arial, Helvetica, sans-serif;
}

.breed-select {
  margin-bottom: 50px;
  padding: 5px 10px;
}

.cat-info {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.content {
  width: 600px;
  padding: 10px;
}

.title {
  text-align: center;
  margin-bottom: 50px;
  font-size: 25px;
}

.description {
  text-indent: 5%;
  font-size: 20px;
  line-height: 1.2;
}

.temperament {
  font-style: italic;
  font-weight: 600;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

